.post{
    display: flex;
    flex-direction: column;
    justify-content: left;
    align-items: center;
}

.postItem{
    margin: 0 5px;
    display: flex;
    justify-content: center;
    align-items: center;
}

.userPhoto{
    border-radius: 50%;
    width: 64px;
}

.postPhoto{
    object-fit: cover;
}

.userId{
    font-weight: bold;
}

@media only screen and (min-width: 768px) {
    /* For desktop: */
    .col-1 {
      flex-basis: 8.33%;
    } /* width works too */
    .col-2 {
      flex-basis: 16.66%;
    }
    .col-3 {
      flex-basis: 25%;
    }
    .col-4 {
      flex-basis: 33.33%;
    }
    .col-5 {
      flex-basis: 41.66%;
    }
    .col-6 {
      flex-basis: 50%;
    }
    .col-7 {
      flex-basis: 58.33%;
    }
    .col-8 {
      flex-basis: 66.66%;
    }
    .col-9 {
      flex-basis: 75%;
    }
    .col-10 {
      flex-basis: 83.33%;
    }
    .col-11 {
      flex-basis: 91.66%;
    }
    .col-12 {
      flex-basis: 100%;
    }
  }